import React, { Component } from 'react'; 
import { withRouter } from 'react-router-dom';


//bootstrap
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

import CarbonxPages from '../../main/carbonx_pages';

//lang & styles
import './buy.css';

import {MAIN_LANG} from '../../main/main_lang';
const msg_main = MAIN_LANG.EN; 

const thisPage = CarbonxPages.filter(el => el.key==="buy")[0];

class BuyPage extends Component {
    // notes:

    render(){
    
        //console.log (thisPage);

        return (
            <>
                <Row><Col>{thisPage.icon}&nbsp; {thisPage.title}</Col></Row>
                <Row><Col><p><br />make sample "cards" of credits for sale by environmental companies</p></Col></Row>
                <Row style={{marginTop: "5px", marginBottom: "10px", marginLeft: "0px", marginRight: "0px"}}>
                    <Col sm={10} md={6} lg={4} className="carbonx-nav-card-col">
                        <Card className="carbonx-nav-card"  >
                            <Card.Body className="carbonx-nav-card-body-title carbonx-nav-card-green">
                                <Card.Title className="carbonx-nav-card-title">Appalachian Solar Co-op</Card.Title>
                            </Card.Body>
                            <Card.Body className="carbonx-nav-card-image" style={{backgroundImage: 'url("./images/applch_coop.jpg")' }}>
                                <Card.Text className="carbonx-nav-card-text-image"></Card.Text>
                            </Card.Body>                               
                            <Card.Body>
                                <Card.Text className="carbonx-nav-card-text">
                                56 Carbon Credits available.
                                </Card.Text>
                            </Card.Body>                            
                            <hr className="carbonx-nav-card-hr"/>
                            <Card.Body className="carbonx-nav-card-body-nav">
                                <Button className="carbonx-button carbonx-nav-card-button" style={{marginRight: "20px"}}>{msg_main.BUY_INFO_BUTTON}</Button>
                                <Button className="carbonx-button carbonx-nav-card-button">{msg_main.BUY_BUTTON}</Button>
                            </Card.Body>
                        </Card>                    
                    </Col>
                    <Col sm={10} md={6} lg={4} className="carbonx-nav-card-col">
                        <Card className="carbonx-nav-card"  >
                            <Card.Body className="carbonx-nav-card-body-title carbonx-nav-card-green">
                                <Card.Title className="carbonx-nav-card-title">TN Urban Forestry Group</Card.Title>
                            </Card.Body>
                            <Card.Body className="carbonx-nav-card-image" style={{backgroundImage: 'url("./images/TNUrbanForestry.jpg")' }}>
                                <Card.Text className="carbonx-nav-card-text-image"></Card.Text>
                            </Card.Body>                               
                            <Card.Body>
                                <Card.Text className="carbonx-nav-card-text">
                                1000 Carbon Credits available.
                                </Card.Text>
                            </Card.Body>                            
                            <hr className="carbonx-nav-card-hr"/>
                            <Card.Body className="carbonx-nav-card-body-nav">
                                <Button className="carbonx-button carbonx-nav-card-button" style={{marginRight: "20px"}}>{msg_main.BUY_INFO_BUTTON}</Button>
                                <Button className="carbonx-button carbonx-nav-card-button">{msg_main.BUY_BUTTON}</Button>
                            </Card.Body>
                        </Card>                    
                    </Col>
                    <Col sm={10} md={6} lg={4} className="carbonx-nav-card-col">
                        <Card className="carbonx-nav-card"  >
                            <Card.Body className="carbonx-nav-card-body-title carbonx-nav-card-green">
                                <Card.Title className="carbonx-nav-card-title">Blount Sequester Farmers</Card.Title>
                            </Card.Body>
                            <Card.Body className="carbonx-nav-card-image" style={{backgroundImage: 'url("./images/blount-seq.jpg")' }}>
                                <Card.Text className="carbonx-nav-card-text-image"></Card.Text>
                            </Card.Body>                               
                            <Card.Body>
                                <Card.Text className="carbonx-nav-card-text">
                                150 Carbon Credits available.
                                </Card.Text>
                            </Card.Body>                            
                            <hr className="carbonx-nav-card-hr"/>
                            <Card.Body className="carbonx-nav-card-body-nav">
                                <Button className="carbonx-button carbonx-nav-card-button" style={{marginRight: "20px"}}>{msg_main.BUY_INFO_BUTTON}</Button>
                                <Button className="carbonx-button carbonx-nav-card-button">{msg_main.BUY_BUTTON}</Button>
                            </Card.Body>
                        </Card>                    
                    </Col>                    
                    <Col sm={10} md={6} lg={4} className="carbonx-nav-card-col">
                        <Card className="carbonx-nav-card"  >
                            <Card.Body className="carbonx-nav-card-body-title carbonx-nav-card-green">
                                <Card.Title className="carbonx-nav-card-title">Manatoba Solar Farm</Card.Title>
                            </Card.Body>
                            <Card.Body className="carbonx-nav-card-image" style={{backgroundImage: 'url("./images/mantoba-solar-farm.jpg")' }}>
                                <Card.Text className="carbonx-nav-card-text-image"></Card.Text>
                            </Card.Body>                               
                            <Card.Body>
                                <Card.Text className="carbonx-nav-card-text">
                                20,000 Carbon Credits available.
                                </Card.Text>
                            </Card.Body>                            
                            <hr className="carbonx-nav-card-hr"/>
                            <Card.Body className="carbonx-nav-card-body-nav">
                                <Button className="carbonx-button carbonx-nav-card-button" style={{marginRight: "20px"}}>{msg_main.BUY_INFO_BUTTON}</Button>
                                <Button className="carbonx-button carbonx-nav-card-button">{msg_main.BUY_BUTTON}</Button>
                            </Card.Body>
                        </Card>                    
                    </Col>
                </Row>              

            </>
        );
    }
}


export default withRouter(BuyPage);
//export default BuyPage;